import {React, useEffect, useState} from 'react';
import 	{ useNavigate } from 'react-router-dom';
import PageWrapper from '../../../../layout/PageWrapper/PageWrapper';
import Page from '../../../../layout/Page/Page';
import { cirurgicalFlow } from '../../../../menu';
import Modal, {ModalBody,ModalFooter,ModalHeader,ModalTitle} from '../../../../components/bootstrap/Modal';
import "./style.css"
import API from '../../../../Services'

import Header from '../../../../components/Header';
import {DropdownItem, DropdownMenu, DropdownToggle} from '../../../../components/bootstrap/Dropdown';
import Dropdown from '../../../../components/bootstrap/Dropdown';
import Button, {ButtonGroup} from '../../../../components/bootstrap/Button';


const Listagem = () => {

	const [loading, setLoading] = useState(true)
	const [patients, setPatients] = useState([])
	const navigate = useNavigate()	

	// eslint-disable-next-line no-unused-vars
	const [ModalCirurgiaCancelada, 	setModalCirurgiaCancelada] 	= useState(false)


	

	const session = JSON.parse(localStorage.getItem('CAPP-local'))
	useEffect(() => {
		
		buscarPacientes()
		
		if(localStorage.getItem('user')){
			const {COD_USUARIO} = JSON.parse( localStorage.getItem('user') )
			avancarEtapasCirurgia( COD_USUARIO )
		}
		
	}, []);

	

	const avancarEtapasCirurgia = async cdUsuario =>{
		console.log('avancarEtapasCirurgia', cdUsuario);
		const paciente =  JSON.parse( localStorage.getItem('paciente') )
		const dataTime = new Date
		const d = new Date(dataTime.getTime())


		const params = {
			avisoCirurgia: paciente.COD_AVISO,		
			dtEtapa: d,
			cdEtapa: 51,
			usuario: cdUsuario,
			salaCirurgia: session.CODIGO_SALA_CIRURGICA,
			tipoAcao: 'I',
			sublocalidade:  session.CODIGO_SUBLOCALIDADE
		}


		const avancar = await API.post('/avancarEtapasCirurgia',  params);
		console.log('avancarEtapasCirurgia', avancar);
		localStorage.removeItem('user')
		localStorage.removeItem('checklist')

		setTimeout(
			await redirecionarPagina()
		, 50000)
	}

	const redirecionarPagina = async()=>{
			
		navigate('/salaCirurgica/acoes')

		localStorage.removeItem('paciente') 


	}


	const buscarPacientes = () => {
		API.get(`/retornaCirurgiasAgendadasByLocalidade?cd_localidade=4&status=1`)
		   .then(response => {
				const {data} = response
			 	setPatients(data)
				setLoading(false)
		   })
	}
	/**
	 * 
	 * @TODO definir código para próxima etapa
	 */
	const selecionarPaciente = async param => {
		
		delete param.PROX_ETAPA;
		delete param.CANCELADO;

		

		navigate(`/salaCirurgica/AssinaturaRecebimento`,{
			state: {
				CODIGO_LOCALIDADE: session.CODIGO_LOCALIDADE,
				CODIGO_SUB_LOCALIDADE: session.CODIGO_SUBLOCALIDADE,
				CODIGO_SALA_CIRURGICA: session.CODIGO_SALA_CIRURGICA,
				CODIGO_PROX_ETAPA: 60,
				...param
			}
		})
		
	}


	const alterarLocalidade = () => {

		localStorage.removeItem('CAPP-local');
		navigate('/')
	
	};
	

	

	const localidadeHeader = `Sala ${ session.SUBLOCALIDADE === 'Hemodinamica' ? session.SUBLOCALIDADE : session.SUBLOCALIDADE.split(' ')[1]}`;


		return (
			
			<PageWrapper title={cirurgicalFlow.SALA_CIRURGICA_LISTAGEM.text}>
			
				<Header titulo={localidadeHeader} localidade='Lista de Pacientes'>
				<Dropdown >
					<DropdownToggle>
						<Button
							color="light"
							icon="menu"
							isLight
						>
							<p> </p>
						</Button>
					</DropdownToggle>
					<DropdownMenu>
						<DropdownItem onClick={() => alterarLocalidade()}>
							Alterar Local
						</DropdownItem>
					</DropdownMenu>
				</Dropdown>
			</Header>
				<Page>
				
				<div>
					
				{
					
					loading  ?
						<div className="lds-ring"><div /><div /><div /><div /></div>
					:					
						patients.length ?	
							patients.map((i) => {
								return (
									// eslint-disable-next-line jsx-a11y/no-static-element-interactions
									<div key={i.CD_AVISO} className={ i.CANCELADO == null ? 'bloco-paciente' : 'bloco-paciente-cancelado' }  onClick={ i.CANCELADO == null ? ()=>selecionarPaciente(i) : ()=>setModalCirurgiaCancelada(true)} onKeyDown={()=>selecionarPaciente(i)}>
									<div className='fw-bold'>
											{i.PACIENTE}
										</div>											
										<div className='text-muted'>
											<small>{i.NM_MAE}</small>
										</div>
										<div className='data_nasc'>
											{i.DT_NASCIMENTO}
										</div>
									</div>
								
								);
							})
							:
							<div className="d-flex justify-content-center">
								<br /><br />
								<h1 style={{color: "#dfdddd", position: "relative", top: "200px"}}>Não há paciente</h1>
							</div>

              		 
               }
			   </div>
				

			   <Modal
					id='modalCancelamentoCirurgia'
					isOpen={ModalCirurgiaCancelada}
					titleId='modalCancelamentoCirurgia'>
						<ModalHeader style={{"backgroundColor":"#E5684C"}}>
							<ModalTitle style={{"color": "#fff"}} id='example-title'> 
								ALERTA
							</ModalTitle>
						</ModalHeader>
						<ModalBody className='ModalBody'>
							<h1>Esta cirurgia foi suspensa!</h1>
						</ModalBody>
						<ModalFooter>
							<ButtonGroup>
									<Button onClick={()=> setModalCirurgiaCancelada(false)} className='btn-style' color='light'>
										Fechar
									</Button>
							</ButtonGroup>
						</ModalFooter>
				</Modal>	
				
				</Page>
			</PageWrapper>
		);

	
};

export default Listagem;
